.twin-impact-image-upload-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.twin-impact-image-upload-container input[type="file"] {
    margin-bottom: 15px;
    padding: 10px;
    border: 2px dashed #cccccc;
    background-color: #f9f9f9;
    border-radius: 8px;
    width: 100%;
    max-width: 400px;
    text-align: center;
    transition: background-color 0.3s ease;
    cursor: pointer;
}

.twin-impact-image-upload-container input[type="file"]:hover {
    background-color: #e0e0e0;
}

.cropper-container {
    width: 100%;
    max-width: 600px;
    margin-bottom: 20px;
}

.cropper-container canvas {
    max-width: 100%;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.spell-image {
    /* 平行四辺形にクリップ */
    clip-path: polygon(10% 0%, 100% 0%, 90% 100%, 0% 100%);
}