@font-face {
  font-family: 'LineSeedJP';
  src: url('/public/fonts/LINESeedJP_TTF_Rg.ttf') format('truetype');
  font-weight: normal;
}

@font-face {
  font-family: 'LineSeedJP';
  src: url('/public/fonts/LINESeedJP_TTF_Bd.ttf') format('truetype');
  font-weight: bold;
}

body {
  /* font-family: 'LineSeedJP', sans-serif; */
  font-family: "Noto Serif JP", serif;
}

/* 全体のレイアウト */
.app-container {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  gap: 40px;
  font-family: 'LineSeedJP', sans-serif;
  /* 統一 */
}

/* ボタン共通のスタイル */
button,
.download-button,
.crop-button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover,
.download-button:hover,
.crop-button:hover {
  background-color: #45a049;
}

/* フォームレイアウト */
.form-container,
.preview-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.form-container label {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 5px;
}

.form-container input,
.form-container select,
.form-container textarea {
  width: 100%;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 14px;
}

/* プレビューエリア */
.preview-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 10px;
  background-color: #f9f9f9;
}

.preview-container img {
  max-width: 100%;
  height: auto;
}

.download-button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s ease;
}

.download-button:hover {
  background-color: #45a049;
}