/* ベーススタイル */
* {
    box-sizing: border-box;
    /* パディングとボーダーを含んで幅を計算 */
}

html,
body {
    margin: 0;
    padding: 0;
    width: 100%;
    overflow-x: hidden;
    /* 横方向のスクロールを無効にする */
}

.card-form-container {
    display: flex;
    flex-direction: column;
    padding: 10px;
    align-items: center;
    width: 100%;
    /* 親要素の幅に合わせる */
}

.form-and-preview {
    display: flex;
    width: 100%;
    justify-content: space-between;
    /* フォームとプレビューを左右に配置 */
}

.form-container,
.card-preview {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    /* 画面幅を超えないように制限 */
}

.card-form {
    padding: 10px;
    max-width: 100%;
    /* コンテナが画面幅を超えないようにする */
}

.card-form .MuiTextField-root,
.card-form .MuiFormControl-root,
.card-form .MuiSlider-root {
    width: 100%;
}

.card-preview-frame {
    margin-bottom: 10px;
    max-width: 100%;
    /* プレビューコンテナが画面幅を超えないようにする */
}

/* 横並びにするためのスタイル */
.slider-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 20px;
}

/* 各スライダーとラベルのスタイル */
.slider-input {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* ラベルのスタイル（Material-UIのラベル風） */
.custom-label {
    font-size: 14px;
    font-weight: 500;
    color: #666;
    margin-bottom: 8px;
    text-transform: uppercase;
}

/* セレクトボックスのスタイル */
.custom-select {
    width: 100%;
    padding: 10px 12px;
    font-size: 16px;
    color: #333;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: none;
    appearance: none;
    transition: border-color 0.3s ease;
}

/* セレクトボックスのフォーカス時のスタイル */
.custom-select:focus {
    border-color: #3f51b5;
    outline: none;
    box-shadow: 0 0 0 2px rgba(63, 81, 181, 0.2);
}

/* optgroup（グループ）のスタイル */
.custom-optgroup {
    font-weight: bold;
    color: #555;
}

/* option（選択肢）のスタイル */
.custom-select option {
    padding: 10px;
    font-size: 14px;
}

/* optgroup のスタイル */
optgroup {
    font-weight: bold;
    color: #666;
    padding: 5px;
}

/* デスクトップ向けのスタイル */
@media (min-width: 768px) {
    .card-form-container {
        flex-direction: row;
        justify-content: center;
        align-items: flex-start;
        gap: 40px;
    }

    .form-container {
        width: 50%;
    }

    .card-preview {
        width: 45%;
    }

    .card-form {
        padding: 40px;
    }

    .preview-container {
        width: 350px;
        text-align: center;
    }

    .sticky-preview {
        position: -webkit-sticky;
        position: sticky;
        top: 20px;
    }
}

/* モバイル向けのスタイル */
@media (max-width: 767px) {

    .form-and-preview {
        flex-direction: column;
    }

    .form-container,
    .card-preview {
        max-width: 100%;
    }

    .card-form {
        padding: 20px;
    }

    .card-form .MuiTextField-root,
    .card-form .MuiFormControl-root,
    .card-form .MuiSlider-root {
        max-width: 100%;
    }

    .preview-container {
        width: 100%;
    }

    .sticky-preview {
        position: relative;
        top: auto;
    }
}